import { useSelector } from "react-redux";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import AddInstituteAcademicDepartmentList from "./AddInstituteAcademicDepartmentList";
import InstituteAcademicDepartmentListCard from "./InstituteAcademicDepartmentListCard";
import { useInstituteAllAcademicDepartment } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
const InstituteAcademicDepartmentList = () => {
  const { t } = useTranslation();
  const instituteId = useSelector((state) => state.idChange.id);
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    instituteAllAcademicDepartment,
    instituteAllAcademicDepartmentLoading,
    instituteAllAcademicDepartmentRefetch,
  } = useInstituteAllAcademicDepartment({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllAcademicDepartment?.list?.length) {
      let obj = nextPage(instituteAllAcademicDepartment?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllAcademicDepartment?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    instituteAllAcademicDepartmentRefetch();
  }, [instituteAllAcademicDepartmentRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <CreateButton
            label={t("add_new_dept")}
            customStyle={{
              margin: "0",
              padding: "0.4rem 1rem",
              minWidth: "fit-content",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
        }}
      >
        {instituteAllAcademicDepartment?.list?.map((department, index) =>
          instituteAllAcademicDepartment?.list?.length === index + 1 ? (
            <div key={department?._id} ref={ref}>
              <InstituteAcademicDepartmentListCard
                department={department}
                onRefetch={onRefetch}
                instituteId={instituteId}
              />
            </div>
          ) : (
            <InstituteAcademicDepartmentListCard
              department={department}
              key={department?._id}
              onRefetch={onRefetch}
              instituteId={instituteId}
            />
          )
        )}
        {instituteAllAcademicDepartmentLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          instituteAllAcademicDepartment?.list?.length === 0 && (
            <EmptyMessage title="No department found related this search." />
          )
        ) : (
          instituteAllAcademicDepartment?.list?.length === 0 && (
            <EmptyMessage title="No department." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddInstituteAcademicDepartmentList
          onClose={onToggle}
          instituteId={instituteId}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default InstituteAcademicDepartmentList;
