import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import InstituteStaff from "../../../UserStaffMember/InstituteStaff/InstituteStaff";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import StringLength from "../../../../Custom/Validation/StringLength";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useInstituteAddAcademicDepartment } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const AddInstituteAcademicDepartmentList = ({
  instituteId,
  onClose,
  onRefetch,
  onEditAction,
  menuData,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    dName: "",
    dTitle: "",
    sid: "",
    gr_initials: "",
  });
  const [staff, setStaff] = useState("");
  const [step, setStep] = useState(1);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [instituteAddAcademicDepartment] = useInstituteAddAcademicDepartment();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        dName: menuData?.dName ?? "",
        dTitle: menuData?.dTitle ?? "",
        sid: menuData?.dHead?._id ?? "",
        gr_initials: menuData?.gr_initials ?? "",
      });
      setStaff(menuData?.dHead);
    }
  }, [isEdit, menuData]);

  const onInputChange = (e, lnt) => {
    if (e.target.value?.length <= lnt) {
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
    }
  };

  const onSelectStaff = (val) => {
    setStaff(val);
    setState((prev) => ({
      ...prev,
      sid: val?._id,
    }));
    setStep(2);
  };

  const onCreate = () => {
    let validation = requiredField(state, ["sid", "gr_initials"]);
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEditAction(state);
      } else {
        if (instituteId) {
          setDisabled((pre) => !pre);
          instituteAddAcademicDepartment({
            id: instituteId,
            bodyParameter: state,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info("dsfdsg", state);

  return (
    <>
      {step === 3 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(2)}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t(isEdit ? "edit_department" : "add_new_dept")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("department_name")}
                placeholder={t("dept_example")}
                name={"dName"}
                value={state.dName}
                type={"text"}
                onChange={(e) => onInputChange(e, 45)}
                errorShow={errorField["dName"]}
              />
              <StringLength maxLength={45} valueLength={state.dName} />

              <div
                className={style.card_container_image}
                onClick={() => setStep(3)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    staff?.staffProfilePhoto
                      ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  {!staff?.staffFirstName ? (
                    <>
                      <h6>
                        {t("assign_dhead")} {t("optional")}
                      </h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${staff?.staffFirstName ?? ""} 
                    ${staff?.staffMiddleName ?? ""}
                    ${staff?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>

              <FormInput
                label={t("give_dhead_title")}
                placeholder={t("dhead_example")}
                name={"dTitle"}
                value={state.dTitle}
                type={"text"}
                onChange={(e) => onInputChange(e, 40)}
                errorShow={errorField["dTitle"]}
              />
              <StringLength maxLength={40} valueLength={state.dTitle} />

              <FormInput
                label={t("department_initials")}
                placeholder={t("department_initials_placeholder")}
                name={"gr_initials"}
                value={state.gr_initials}
                type={"text"}
                onChange={(e) => onInputChange(e, 10)}
              />
              <StringLength maxLength={10} valueLength={state.gr_initials} />

              <CreateButton
                label={isEdit ? "update" : "create"}
                onAction={onCreate}
              />
            </UserMemberContentWrapper>

            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddInstituteAcademicDepartmentList;
