import { Link, useParams } from "react-router-dom";
import { assestsAcademicDepartmentUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "../InstituteMember.module.css";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useInstituteCountStats } from "../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const InstituteAcademicDepartmentSidebar = () => {
  const { t } = useTranslation();
  const params = useParams();
  const instituteId = useSelector((state) => state.idChange.id);

  const { instituteCountStats } = useInstituteCountStats({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  return (
    <>
      <img
        src={`${assestsAcademicDepartmentUrl}/cover.svg`}
        alt="department cover icon"
        className={style.iads_cover}
      />
      <section
        className={style.iads_card}
        style={{
          marginTop: "-1.5rem",
          borderTopLeftRadius: "1rem",
          borderTopRightRadius: "1rem",
          backgroundColor: "#ffffff",
          position: "relative",
          padding: "0",
        }}
      >
        <Link
          to={`/q/${params.username}/institute/member/academic/department`}
          className={`${style.iads_card} ${style.iads_card_active}`}
          // style={{
          //   padding:"0"
          // }}
        >
          <div className={style.iadsc_inner}>
            <h6>{t("departments")}</h6>
            <p>{t("dept_in_ins")}</p>
          </div>
          <h6>{instituteCountStats?.departmentCount ?? 0}</h6>
        </Link>
      </section>

      <Link to={`/q/${params.username}/staff`} className={style.iads_card}>
        <div className={style.iadsc_inner}>
          <h6>{t("total_staffs")}</h6>
          <p>{t("all_staff_working")}</p>
        </div>
        <h6>{instituteCountStats?.staffCount ?? 0}</h6>
      </Link>
      <Link to={`/q/${params.username}/student`} className={style.iads_card}>
        <div className={style.iadsc_inner}>
          <h6>{t("total_students")}</h6>
          <p>{t("current_plus")}</p>
        </div>
        <h6>{instituteCountStats?.studentCount ?? 0}</h6>
      </Link>
      <Link
        to={`/q/${params.username}/student?a=unapproved`}
        className={style.iads_card}
        style={{
          borderBottom: "0",
        }}
      >
        <div className={style.iadsc_inner}>
          <h6>{t("total_unapproved_students")}</h6>
          <p>{t("current_plus")}</p>
        </div>
        <h6>{instituteCountStats?.un_approved_student_count ?? 0}</h6>
      </Link>
    </>
  );
};

export default InstituteAcademicDepartmentSidebar;
