import { Link, useParams } from "react-router-dom";
import { assestsAcademicDepartmentUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../InstituteFeedback/InstituteFeedback.module.css";
const InstituteAcademicDepartmentListCard = ({ department }) => {
  const params = useParams();
  return (
    <Link
      to={`/q/${params.username}/institute/member/academic/department/detail`}
      state={{
        departmentId: department?._id,
        departmentName: department?.dName,
      }}
    >
      <div
        className={style.sf_card_hover}
        style={{
          padding: "1rem",
          boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
          marginBottom: "1rem",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsAcademicDepartmentUrl}/profile.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              borderRadius: "0",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {department?.dName ?? ""}
            </h6>
            {department?.dHead?.staffFirstName ? (
              <p
                style={{
                  color: "#121212",
                }}
              >
                {`${department?.dHead?.staffFirstName ?? ""} ${
                  department?.dHead?.staffMiddleName ?? ""
                } ${department?.dHead?.staffLastName ?? ""}`}
              </p>
            ) : null}

            <p>{department?.dTitle ?? ""}</p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default InstituteAcademicDepartmentListCard;
